const cacheUtil = {
  current: '',
  setCurrent: function (year) {
    if (this.current !== year) {
      this.current = year
      this.clear()
    }
  },
  set: function (key, value) {
    if (!cache) cache = {}
    cache[key] = value
    return cache[key]
  },
  get: function (key) {
    if (!cache) cache = {}
    return cache[key]
  },
  clear: function () {
    cache = null
  }
}

const formatDayD4 = function (month, day) {
  month = month + 1
  month = month < 10 ? '0' + month : month
  day = day < 10 ? '0' + day : day
  return 'd' + month + day
}

