<template>
  <div>
    <h3>Slots</h3>

    <h4>shortcut</h4>

    <div class="date-label"></div>
    <date-picker type="date" v-model="date" :shortcuts="shortcuts1"/>

    <div class="date-label"></div>
    <date-picker type="date" v-model="rdate" :shortcuts="shortcuts2" range/>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="date">
      <template v-slot:shortcut>
        <div class="b1">
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
        </div>
      </template>
    </date-picker>
    <date-picker type="date" v-model="date">
      <template v-slot:shortcut>
        <div class="b1">
          <div @click="onShortcutClick('date', '-1y')"></div>
          <div @click="onShortcutClick('date', '-1m')"></div>
          <button @click="onShortcutClick('date', '-1d')"></button>
        </div>
      </template>
    </date-picker>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="rdate" range>
      <template v-slot:shortcut>
        <div class="b1">
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
        </div>
      </template>
    </date-picker>
    <date-picker type="date" v-model="rdate" range>
      <template v-slot:shortcut>
        <div class="b1">
          <div @click="onShortcutClick('rdate','-1y')"></div>
          <div @click="onShortcutClick('rdate','-1m')"></div>
          <button @click="onShortcutClick('rdate','-1d')"></button>
        </div>
      </template>
    </date-picker>

    <h4>value</h4>
    <div class="date-label"> - </div>
    <date-picker type="date" v-model="date">
      <template v-slot:value="{value, format, type, visible}">
        <ul class="custom-value">
          <li>visible: {{ visible }}</li>
          <li>type: {{ type }}</li>
          <li>format: {{ format }}</li>
          <li>value: {{ value }}</li>
        </ul>
      </template>
    </date-picker>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="rdate" range>
      <template v-slot:value="{value, format, type, visible}">
        <ul class="custom-value">
          <li>visible: {{ visible }}</li>
          <li>type: {{ type }}</li>
          <li>format: {{ format }}</li>
          <li>value: {{ value }}</li>
        </ul>
      </template>
    </date-picker>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="date">
      <template v-slot:title>
        <span></span>
      </template>
    </date-picker>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="rdate" range>
      <template v-slot:title>
        <div></div>
        <div></div>
      </template>
    </date-picker>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="date">
      <template v-slot:footer>
        <span> <code>v-slot:footer</code> </span>
        <button></button>
      </template>
    </date-picker>

    <div class="date-label"> - </div>
    <date-picker type="date" v-model="rdate" :visible.sync="visible" range>
      <template v-slot:footer>
        <div class="custom-footer">
          <span> <code>v-slot:footer</code> </span>
          <span class="btn-ok" @click="onOk"></span>
        </div>
      </template>
    </date-picker>
  </div>
</template>

<script>
import mixin from './mixin'
import util from '../assets/script/util'
import DatePicker from "@/components";

export default {
  name: 'SlotsDemo',
  mixins: [mixin],
  data() {
    const date = this.getFixedDate()
    const today = date.getDate()
    return {
      date,
      shortcuts1: [{
        text: '',
        value: util.setDate(date, {date: today - 1})
      }, {
        text: '',
        value: util.setDate(date)
      }, {
        text: '',
        value: util.setDate(date, {date: today + 1})
      }, {
        text: '',
        value: util.setDate(date, {date: today + 7})
      }, {
        text: '',
        value: util.setDate(date, {year: date.getFullYear() - 1})
      }, {
        text: '',
        value: util.setDate(date, {year: date.getFullYear() + 1})
      }],
      shortcuts2: [{
        text: '',
        value: util.getWeekRange(date)
      }, {
        text: '',
        value: [util.setDate(date, {date: today - 6}), date]
      }, {
        text: '',
        value: [util.setDate(date, {date: today - 14}), date]
      }, {
        text: '1',
        value: [util.setDate(date, {month: date.getMonth() - 1, date: today}), date]
      }],
      visible: false
    }
  },
  methods: {
    onOk() {
      this.visible = false
    },
    onShortcutClick(dateName, offset) {
      const value = this[dateName]
      const now = DatePicker.util.getDate()
      if (Array.isArray(value)) {
        this[dateName] = [
          DatePicker.util.offsetDate(now, offset),
          now
        ]
      } else {
        this[dateName] = DatePicker.util.offsetDate(now, offset)
      }
    }
  }
}
</script>

