.b1 {
  padding: 5px;
  width: 100px;

  div {
    margin-bottom: 5px;
    border-bottom: 1px solid red;
    cursor: pointer;

    &:hover {
      color: red;
    }
  }
}

.custom-value {
  border: 1px solid #55a532;

  &:hover {
    border-color: #126ac4;
  }
}

.custom-footer {
  padding: 5px 10px;
  overflow: auto;
}

.btn-ok {
  display: inline-block;
  border: 1px solid #cccccc;
  border-radius: 3px;
  padding: 3px 4px;
  cursor: pointer;
  font-size: 14px;
  float: right;

  &:hover {
    color: #126ac4;
  }
}
</style>
