<template>
  <div>
    <div class="date-label"></div>
    <date-picker type="datetime" v-model="fixedTime" clearable/>
    <div class="tips">
      <div>
        
        <pre><code>
const t = new Date('2012-12-12').getTime();
DatePicker.util.setDate(t)
</code></pre>
      </div>
      <div>: <code>DatePicker.util.setDate(timestamp)</code></div>
      <div></div>
      <div></div>
    </div>
    <div class="date-label"></div>
    <date-picker type="datetime" v-model="date" show-lunar show-festival/>
    <div class="date-label"></div>
    <picker class="calendar" v-model="date" :week-start="1" :mark-function="markFunction"/>
    <div class="tips">M<b></b><b></b></div>
  </div>
</template>

<script>
import DatePicker from "@/components";

export default {
  name: "ADemo",
  data() {
    return {
      date: '',
      fixedTime: '2012-12-12'
    }
  },
  created() {
    DatePicker.util.setDate(new Date(this.fixedTime).getTime())
  },
  watch: {
    fixedTime(v) {
      const d = DatePicker.util.parse(v)
      DatePicker.util.setDate(d.getTime())
    }
  },
  methods: {
    markFunction(e) {
      if (e.type === 'date') {
        if (e.day === 6 || e.day === 0) {
          return '<span class="flag" title="">*</span>';
        }
      } else if (e.type === 'month') {
        if (e.month === 10) {
          return '<span class="flag" title="">*</span>';
        }
      } else if (e.type === 'year') {
        if (e.year === 2012) {
          return '<span class="flag" title="">*</span>';
        }
      }
    }
  }
}
</script>

<style lang="less">
.flag {
  float: right;
  text-align: center;
  color: #0dc50d;
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;


  &:hover {
    color: #ffffff;
    background-color: rgba(20, 189, 20, 0.85);
  }
}

.calendar {
  border: 1px solid #cccccc;
}
</style>
